<template>
  <div class="same_width_height">
    <table-rank
      :showData="showData"
      :total="total"
      @pageChange="getPageData"
    ></table-rank>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "comment-rank",
  computed: {
    // 评论数据页码数据
    ...mapState({
      showData: (state) => state.comments.comments,
      total: (state) => state.comments.total,
    }),
  },
  methods: {
    // 获取页码数据
    getPageData(page) {
      this.$store.dispatch("comments/sendCommentRequest", { page });
    },
  },
  mounted() {
    this.getPageData(1);
  },
};
</script>

